@import "variables";
@import "font-icon/style";

@font-face {
  font-family: $icomoon-font-family;
  font-style: normal;
  font-weight: 400;
  src: url("#{$icons-filepath}/icon-orange.woff2") format("woff2"), url("#{$icons-filepath}/icon-orange.woff") format("woff");
}

[class^="icon-"],
[class*=" icon-"] {
  speak: none;
  font-style: normal;
  font-weight: 400;
  font-variant: normal;
  line-height: 1;
  text-transform: none;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

[class^="icon-"]::after,
[class*=" icon-"]::after,
[class^="icon-"]::before,
[class*=" icon-"]::before {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: icon-orange, sans-serif !important; // stylelint-disable-line declaration-no-important
}
